
import { 
  type HomeViewCard 
} from "@/utils/config-helper";
import { 
  defineComponent,
  type PropType
} from "vue"; 
import StringDiv from "@/components/StringDiv/StringDiv.vue";

export default defineComponent({
  name: "CardOne",
  components: {
    StringDiv,
  },
  props: {
    cardInfo: {
      type: Object as PropType<HomeViewCard>,
      required: true,
    },
    themeName: {
      type: String,
      default: () => {
        return "default";
      },
    },
  },
  setup() {
    return {};
  },
  data: () => {
    return {};
  },
  computed: {
  },
  watch: {
  },
  methods: {
    onClickCardOne() {
      this.$router.push({
        path: "/detail",
        query: {
          title: this.cardInfo.title
        },
      });
    },
  },
});

