
import { defineComponent } from "vue"; 
import LangSwitcher from "../LangSwitcher/LangSwitcher.vue";
import { getAssetConfig } from "@/utils/config-helper";
import { getDetailViewImgFrom } from "@/views/DetailView/util-helper/config-asset";

export default defineComponent({
  name: "PageTopHeader",
  components: {
    LangSwitcher,
  },
  props: {
    isShowLeft: {
      type: Boolean,
      default: () => {
        return true;
      },
    },
  },
  setup() {
    return {};
  },
  data: () => {
    return {};
  },
  computed: {
    logoSrc() {
      const logoSrc = getAssetConfig()?.detailView.imgSrc.logo || "";
      if (logoSrc) {
        return getDetailViewImgFrom(logoSrc);
      }
      return "";
    },
  },
  watch: {
  },
  methods: {
    onClickLeft() {
      this.$router.push({
        path: "/",
      });
    }
  },
});

