
import { getAssetConfig } from "@/utils/config-helper";
import { defineComponent } from "vue"; 
import StringDiv from "@/components/StringDiv/StringDiv.vue";
import { currentLangId } from "@/utils/lang-helper";
import { getHomeViewSrcFrom } from "../../util-helper/asset-config";

export default defineComponent({
  name: "AbstractInfo",
  components: {
    StringDiv,
  },
  props: {
  },
  setup() {
    return {
      currentLangId,
    };
  },
  data: () => {
    return {};
  },
  computed: {
    assetConfig() {
      return getAssetConfig()?.homeView;
    },
    logoSrc() {
      const homeView = getAssetConfig()?.homeView;
      if (homeView) {
        return getHomeViewSrcFrom(homeView.imgSrc.logo);
      }
      return "";
    },
  },
  mounted() {
    console.log("assetConfig", getAssetConfig());
  },
  watch: {
  },
  methods: {
  },
});

