
import { getAssetConfig } from "@/utils/config-helper";
import { defineComponent } from "vue"; 
import StringDiv from "@/components/StringDiv/StringDiv.vue";
import { currentLangId } from "@/utils/lang-helper";
import { getHomeViewSrcFrom } from "../../util-helper/asset-config";

export default defineComponent({
  name: "CircleInfo",
  components: {
    StringDiv,
  },
  props: {
  },
  setup() {
    return {
      currentLangId,
    };
  },
  data: () => {
    return {};
  },
  computed: {
    assetConfig() {
      return getAssetConfig()?.homeView.circleInfo;
    },
    emailHref() {
      if (this.assetConfig) {
        const email = getAssetConfig()?.homeView.circleInfo.email || "";
        return `mailto:${email}`;
      }
      return "";
    },
    pencilSrc() {
      const homeView = getAssetConfig()?.homeView;
      if (homeView) {
        return getHomeViewSrcFrom(homeView.imgSrc.pencil);
      }
      return "";
    },
  },
  watch: {
  },
  methods: {
  },
});

